// Functional Imports
import { useState, useMemo } from "react";
import { useRoutes } from "react-router-dom";
import { useDispatch } from "react-redux";
import { HeaderSliceActions } from "../store/header-slice";
import { useEffect, lazy, Suspense } from "react";
import { useHttpClientWithNavigate } from "../utility/http-hook";
import { useNavigate } from "react-router-dom";

// Components Imports
import Loading from "../componets/Loading/Loading";

// Componets Imports (Lazy)
const Root = lazy(() => import("../Apps/Source/Root"));
const ViewAll = lazy(() => import("../Apps/Source/ViewAll/ViewAll"))


function SourcePages(props){
    const baseURL               = '/source';
    const dispatcher            = useDispatch();
    const sendRequest           = useHttpClientWithNavigate();
    const navigate              = useNavigate();
    const [router, setRouter]   = useState([]);

    const routes = useMemo(() => [
        { 
            index: true, 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <Root baseURL={baseURL} /> 
                </Suspense>
            )
        }, 
        {
            path: 'view',
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <ViewAll baseURL={baseURL} />
                </Suspense>
            )
        }
    ], []);

    useEffect(() => {
        (async () => {
            try{
                const response = await sendRequest({ url: '/apps/source/app-headers' });

                if(!response) return
            
                dispatcher( HeaderSliceActions.setHeaders({ 
                    currentApp: 'Source', 
                    appTabs: response.allowedtabs.map(tab => ({ ...tab, baseURL })),
                    appPermissions: response.permissions
                }) );

                setRouter(routes);
            } catch( err ){
                navigate('/error', { state: { status: err.status, data: err.msg } });
            }
        })();
    }, [dispatcher, navigate, sendRequest, routes]);

    return useRoutes(router);
}

export default SourcePages;